import React from "react";
import requests from "./requests";
import "./banner.css";

function Banner() {
  const [movie, setMovie] = React.useState({});

  React.useEffect(() => {
    async function getMovie() {
      const res = await fetch(requests.fetchNetflixOriginals);
      const resJson = await res.json();
      setMovie(
        resJson.results[Math.floor(Math.random() * resJson.results.length)]
      );
    }

    getMovie();
  }, []);

  return (
    <header className="banner">
      <img
        src={`https://image.tmdb.org/t/p/original/${movie?.backdrop_path}`}
      ></img>

      <div className="content">
        <h1 className="movieName">{movie.name}</h1>
        <button className="button">Play</button>
        <button className="button">Wishlist</button>
        <h2 className="description">{movie.overview}</h2>
      </div>
    </header>
  );
}

export default Banner;
